<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p> AI NLP KOREA </p>
    <img src="../assets/think2.jpeg" width="80%" />
    <P> Who am I...</P>
    <br>

    <div>
      <h3>화 - 자연어처리연구소</h3>
      <p><strong>류민호</strong> (한양대학교 산업공학과/대학원) <br> Encoder Decoder Deep Learning Models for Text Summarization 을 연구 및 구현 </p>
      <p><strong>유정빈</strong> (연세대학교 정보대학원) <br> word2vec 심화 연구 & 코드분석</p>
      <p><strong>박종진</strong> (청운대학교 교수) <br> RNN 심화연구 </p>
      <p><strong>황원요</strong> (대학생/소프트웨어공학과) <br> CNN을 이용한 한국어 문장분류 연구</p>
      <p><strong>전윤길</strong> (데이터분석가) <br> CNN이고 관련논문중에서 Yoon Kim (2014) 논문연구</p>
      <p><strong>김형락</strong> (빅트리/연구직) <br> RNN part 에 문장 수준 분류를 위한 RNN A Convolutional Neural Network for Modelling Sentences-논문연구</p>
      <p><strong>권득신</strong> (신한카드/빅데이터본부 인공지능셀) <br> rnn + attention 연구 및 코드분석</p>
      <br>
    </div>
    <div>
      <h3>목 - 자연어처리연구소</h3>
      <p><strong>임수경</strong> (무하유/개발) <br> RNN - NMT(Neural Machine Translation) 연구</p>
      <p><strong>송성곤</strong> (씨디네트웍스/서버개발) <br> Word2vec - Web Application Firewall 연구</p>
      <p><strong>김O형</strong> (대기업/연구직) <br> RNN - Language Modeling 연구 </p>
      <p><strong>박수상</strong> (자소설닷컴/대표) <br> 어탠션 매커니즘 - Text Summarization 연구</p>
      <p><strong>지도현</strong> ((주)아키핀/대표) <br> CNN - 합성곱신경망을 이용한 문장 분류 연구</p>
      <p><strong>박태현</strong> (중앙대학교/컴퓨터공학과) <br> CNN을 활용한 sentence classification 연구</p>
      <p><strong>장대광</strong> (네이버/개발) <br> RNN - Sequence Tagging (Named Entity Recognition) 연구</p>
      <p><strong>신진환</strong> (아시나아항공) <br> 어탠션 매커니즘을 활용한 뉴스카테고리 분류기 시각화 연구</p>
      <br>
    </div>
    <div>
      <h3>금 - 자연어처리연구소</h3>
      <p><strong>강호준</strong> (서강대학교/석사) <br> CNN 심화연구</p>
      <p><strong>김민정</strong> ((주)크로센트/개발) <br> RNN + 강화학습 연구</p>
      <p><strong>한승현</strong> (아주대학교/석사) <br> Seq2Seq + Attention mechanism 챗봇코드 reverse engineering </p>
      <p><strong>이기선</strong> (고려대/치과의사) <br> Word2Vec 심화연구</p>
      <p><strong>문아라</strong> (클라우드북스/대표) <br> Word2Vec 심화연구</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '딥러닝 자연어처리 연구소'
    }
  }
}

</script>

